@import url(https://fonts.googleapis.com/css2?family=Turret+Road&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Coming+Soon&display=swap);
*{box-sizing:border-box;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;margin:0;padding:0}html{overflow-y:scroll}body{background:#311b92;background-image:url("https://www.transparenttextures.com/patterns/gradient-squares.png");min-height:70vh;padding:1rem;box-sizing:border-box;display:flex;justify-content:center;align-items:center;font-family:"Coming Soon",cursive,cursive;text-align:center;font-size:118%}.container{background:#ffd600;border-radius:10px;box-shadow:4px 3px 7px 2px #00000040;padding:1rem 1rem 2rem;display:flex;flex-direction:column;width:100%;height:auto;min-height:530px;max-width:600px;min-width:450px}.heading{display:flex;align-items:center;justify-content:center;margin:2rem 2rem}.heading h1{color:#fff;transform:rotate(-6deg);padding:0.2rem 1.2rem;border-radius:5px;background-color:#311b92;font-size:1.5rem}.form{margin-bottom:32px}.form.edit{margin-bottom:0}.form input[type=text]{font-family:"Coming Soon",cursive,"cursive"}.form .task-input{padding:15px 32px 15px 16px;border-radius:4px 0 0 4px;border:2px solid #311b92;outline:none;width:320px;background:transparent;color:#333}.form .task-input::-webkit-input-placeholder{color:#333;font-family:"Coming Soon",cursive}.form .task-input:-ms-input-placeholder{color:#333;font-family:"Coming Soon",cursive}.form .task-input::-ms-input-placeholder{color:#333;font-family:"Coming Soon",cursive}.form .task-input::placeholder{color:#333;font-family:"Coming Soon",cursive}.form .task-button{padding:17px;border:none;border-radius:0 4px 4px 0;cursor:pointer;outline:none;background:#311b92;color:#fff;text-transform:capitalize;font-family:"Coming Soon",cursive}.taskItem{display:flex;justify-content:space-between;align-items:center;margin:4px auto;color:#fff;padding:5px 9px;border-radius:6px;width:90%}.taskItem:nth-child(3n+1){background:linear-gradient(90deg, #149fff 0%, rgba(17,122,255,0.8) 100%)}.taskItem:nth-child(3n+2){background:linear-gradient(90deg, #ff7614 0%, rgba(255,84,17,0.8) 100%)}.taskItem:nth-child(3n+3){background:linear-gradient(90deg, #57c84d 0%, #2eb62c 100%)}.taskItem.animate__animated.animate__zoomIn{--animate-duration: .3s}.taskItem.animate__animated.animate__hinge{--animate-duration: .3s}.icons{font-size:20px}.fa-edit{vertical-align:middle;cursor:pointer}.fa-trash-alt{vertical-align:middle;cursor:pointer}ul{list-style-type:none}ul li{text-align:left;position:relative;padding:0.5rem}

